.modal {
  display: none;
  position: fixed;
  z-index: 3;
  left: 0;
  right: 0;
  top: 80;
  margin-left: auto;
  margin-right: auto;
  width: 60%;
  height: 62%;
  overflow: hidden;
  // background-color: rgb(0, 0, 0);
  // background-color: rgba(0, 0, 0, 0.1);
  padding-top: 50px;
  border-radius: 5px;

  @media (max-width: 500px) {
    width: 80%;
  }

  &__box {
    padding: 16px;
    text-align: center;
  }

  &__content {
    background-color: #fefefe;
    margin: 5% auto 15% auto;
    border: 1px solid #888;
    width: 80%;
    border-radius: 5px;
  }
}

