hr {
  border: 1px solid red;
  margin-bottom: 25px;
}

.clearfix {
  button {
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 100%;
    opacity: 0.9;
  }

  button:hover {
    opacity: 1;
  }

  .cancelbtn {
    border-color: #ccc;
    color: black;
  }

  .deletebtn {
    background-color: #f44336;
  }

  .cancelbtn,
  .deletebtn {
    float: left;
    width: 50%;
  }
}

.clearfix::after {
  content: '';
  clear: both;
  display: table;
}
